.button-container {
    position: relative;
    display: flex;
    justify-content: space-between;
    margin-top: 80px;
    width: 60%;
    padding: 0;
}

.button-container .btn {
    height: 80px;
    width: 200px;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    color: var(--text-primary);
    transition: 0.2s ease;
    overflow: hidden;
}

.button-container a {
    position: relative;
    display: flex;
    flex-direction: column;
    font-size: 18px;
}

.button-container .btn.pri {
    border: 2px solid var(--color-secundary-light);
}

.button-container .btn.pri:hover {
    border: none;
    background-color: var(--color-secundary-light);
}

.button-container .btn.pri::before {
    position: absolute;
    content: '';
    height: 100%;
    width: 0%;
    left: 0;
    transition: 0.2s ease;
}


.button-container .btn.pri::after {
    position: absolute;
    content: '';
    height: 100%;
    width: 0%;
    left: 0;
    transition: 0.4s ease;
}

.button-container .btn.pri:hover::before {
    position: absolute;
    content: '';
    height: 100%;
    width: 65%;
    left: 0;
    background-color: rgba(255, 255, 255, 0.274);
}

.button-container .btn.pri:hover::after {
    position: absolute;
    content: '';
    height: 100%;
    width: 50%;
    left: 0;
    background-color: rgba(255, 255, 255, 0.247);
}

.button-container .btn.sec {
    background-color: var(--color-primary-light);
}

.button-container .btn.sec:hover {
    border: 2px solid var(--color-primary);
}

.button-container .btn.sec::before {
    position: absolute;
    content: '';
    height: 100%;
    width: 65%;
    left: 0;
    background-color: rgba(255, 255, 255, 0.164);
    transition: 0.2s ease;
}



.button-container .btn.sec::after {
    position: absolute;
    content: '';
    height: 100%;
    width: 50%;
    left: 0;
    background-color: rgba(255, 255, 255, 0.295);
    transition: 0.2s ease;
}

.button-container .btn.sec:hover::before {
    position: absolute;
    content: '';
    height: 100%;
    width: 0;
    left: 0%;
}

.button-container .btn.sec:hover::after {
    position: absolute;
    content: '';
    height: 100%;
    width: 0;
    left: 0%;
}